
"use client";

import type { FocusEvent } from 'react';
import { useState, useEffect } from 'react';
import type { CellData } from '@/types/spreadsheet';
import { Input } from "@/components/ui/input";
import { cn } from '@/lib/utils';

interface CellProps {
  cellData: CellData;
  rowIndex: number;
  colIndex: number;
  onCellChange: (rowIndex: number, colIndex: number, rawValue: string | number) => void;
  sheetId: string; 
  isSelected: boolean;
  onSelect: () => void;
}

export default function Cell({ cellData, rowIndex, colIndex, onCellChange, sheetId, isSelected, onSelect }: CellProps) {
  const [isEditing, setIsEditing] = useState(false);
  const [editValue, setEditValue] = useState(cellData.rawValue || '');

  useEffect(() => {
    if (!isEditing) {
      setEditValue(cellData.rawValue || '');
    }
  }, [cellData.rawValue, isEditing]);

  const handleDoubleClick = () => {
    onSelect(); // Ensure cell is selected before editing
    setIsEditing(true);
  };

  const handleClick = () => {
    onSelect();
  }

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setEditValue(e.target.value);
  };

  const handleBlur = (e: FocusEvent<HTMLInputElement>) => {
    if (e.relatedTarget && e.currentTarget.contains(e.relatedTarget as Node)) {
        return;
    }
    setIsEditing(false);
    if (String(editValue) !== String(cellData.rawValue)) { // Compare as strings to handle type differences
      onCellChange(rowIndex, colIndex, editValue);
    }
  };
  
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' || e.key === 'Tab') {
      e.preventDefault(); // Prevent default Tab behavior
      setIsEditing(false);
      (e.target as HTMLInputElement).blur(); // Trigger blur to save
      // TODO: Implement navigation on Enter/Tab
    } else if (e.key === 'Escape') {
      setEditValue(cellData.rawValue || ''); 
      setIsEditing(false);
      (e.target as HTMLInputElement).blur(); 
    }
  };

  const cellStyleFromData = cellData.style || {};
  const appliedStyle: React.CSSProperties = {
    fontWeight: cellStyleFromData.bold ? 'bold' : 'normal',
    fontStyle: cellStyleFromData.italic ? 'italic' : 'normal',
    textDecoration: cellStyleFromData.underline ? 'underline' : 'none',
    color: cellStyleFromData.fontColor, // Will be undefined if not set, CSS default takes over
    backgroundColor: cellStyleFromData.backgroundColor, // Will be undefined if not set
    textAlign: cellStyleFromData.textAlign as React.CSSProperties['textAlign'], // Cast for safety
  };

  if (isEditing) {
    return (
      <td className={cn("p-0 border relative", {"ring-2 ring-primary ring-inset": isSelected})}>
        <Input
          type="text"
          value={editValue}
          onChange={handleChange}
          onBlur={handleBlur}
          onKeyDown={handleKeyDown}
          className="w-full h-full p-1.5 box-border text-sm outline-none rounded-none border-transparent focus:ring-0 focus:border-primary"
          style={appliedStyle}
          autoFocus
          aria-label={`Edit cell ${cellData.id}`}
        />
      </td>
    );
  }

  return (
    <td
      className={cn(
        "p-1.5 border cursor-cell text-sm truncate min-w-[100px] relative", // Added relative for selection outline
        {"font-bold": cellData.style?.bold},
        {"italic": cellData.style?.italic},
        {"underline": cellData.style?.underline},
        cellData.style?.textAlign === 'left' && "text-left",
        cellData.style?.textAlign === 'center' && "text-center",
        cellData.style?.textAlign === 'right' && "text-right",
        // isSelected && "bg-primary/10", // Subtle background for selection
      )}
      style={appliedStyle}
      onClick={handleClick}
      onDoubleClick={handleDoubleClick}
      tabIndex={0} 
      onFocus={onSelect} // Select on focus as well (keyboard navigation)
      aria-readonly={true}
    >
      {isSelected && !isEditing && (
          <div className="absolute inset-0 border-2 border-primary pointer-events-none rounded-[1px]" />
      )}
      {/* Ensure value is stringified to avoid React errors with object/undefined children */}
      {String(cellData.value ?? '')}
    </td>
  );
}

    