
"use client";

import React from 'react'; // Import React
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { useSpreadsheet } from "@/hooks/useSpreadsheet";
import { OfflineSheetLogo } from "../icons/OfflineSheetLogo";
import Link from "next/link";
import { Save, Home, Upload, Download, Undo, Redo, Bold, Italic, Underline, Palette, AlignLeft, AlignCenter, AlignRight, Combine, Columns, RowsIcon } from "lucide-react";
import {
  Menubar,
  MenubarContent,
  MenubarItem,
  MenubarMenu,
  MenubarSeparator,
  MenubarShortcut,
  MenubarTrigger,
} from "@/components/ui/menubar"
import { cn } from '@/lib/utils';

interface ToolbarProps {
  spreadsheetName: string;
}

export default function Toolbar({ spreadsheetName }: ToolbarProps) {
  const { 
    saveSpreadsheet, 
    spreadsheet, 
    setSpreadsheet, 
    selectedCell, 
    updateSelectedCellStyle,
    undo,
    redo,
    canUndo,
    canRedo
  } = useSpreadsheet();

  const handleNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (spreadsheet && setSpreadsheet) {
      // Use the setSpreadsheet from context that updates history
      // For name change, we might want a more specific action in context if it needs special undo/redo handling
      // For now, directly updating and relying on the history wrapper in context for general changes.
      // This specific 'setSpreadsheet' should be the one that triggers history.
      // If we need fine-grained history for name changes, context would need a 'renameSpreadsheet' action.
      // For now, let's assume `setSpreadsheet` in `useSpreadsheet` is `setSpreadsheetWithHistory`
      setSpreadsheet(prev => prev ? ({ ...prev, name: e.target.value, updatedAt: Date.now() }) : null);
    }
  };

  const selectedCellStyle = React.useMemo(() => {
    if (!spreadsheet || !selectedCell) return {};
    const sheet = spreadsheet.sheets.find(s => s.id === selectedCell.sheetId);
    if (!sheet) return {};
    const cell = sheet.cells[selectedCell.rowIndex]?.[selectedCell.colIndex];
    return cell?.style || {};
  }, [spreadsheet, selectedCell]);

  const handleToggleBold = () => {
    if (selectedCell) updateSelectedCellStyle({ bold: !selectedCellStyle?.bold });
  };
  const handleToggleItalic = () => {
    if (selectedCell) updateSelectedCellStyle({ italic: !selectedCellStyle?.italic });
  };
  const handleToggleUnderline = () => {
    if (selectedCell) updateSelectedCellStyle({ underline: !selectedCellStyle?.underline });
  };

  const handleExport = (format: 'xlsx' | 'csv') => console.log(`Export as ${format} (Not implemented)`);
  const handleLoadFromFile = () => console.log("Load from file (Not implemented)");
  
  // TODO: Implement actual cell formatting logic
  const insertRow = () => console.log("Insert Row (Not implemented)");
  const insertColumn = () => console.log("Insert Column (Not implemented)");
  const mergeCells = () => console.log("Merge Cells (Not implemented)");
  const setFontColor = () => console.log("Set Font Color (Not implemented)");
  const setBackgroundColor = () => console.log("Set Background Color (Not implemented)");
  const setTextAlign = (align: 'left' | 'center' | 'right') => {
    if (selectedCell) updateSelectedCellStyle({ textAlign: align });
  };


  return (
    <div className="p-2 border-b bg-card flex items-center justify-between gap-2 shadow-sm flex-wrap print:hidden">
      <div className="flex items-center gap-2">
        <Link href="/" aria-label="Back to dashboard">
          <OfflineSheetLogo className="h-8 w-8"/>
        </Link>
        <Input 
          value={spreadsheetName} 
          onChange={handleNameChange}
          className="font-semibold text-lg w-auto min-w-[150px] max-w-[300px]"
          aria-label="Spreadsheet Name"
          disabled={!spreadsheet}
        />
      </div>

      <div className="flex items-center gap-1 flex-wrap">
        <Button variant="ghost" size="icon" onClick={saveSpreadsheet} aria-label="Save Spreadsheet" disabled={!spreadsheet}><Save /></Button>
        <Button variant="ghost" size="icon" onClick={undo} aria-label="Undo" disabled={!canUndo}><Undo /></Button>
        <Button variant="ghost" size="icon" onClick={redo} aria-label="Redo" disabled={!canRedo}><Redo /></Button>
        
        <Separator orientation="vertical" className="h-6 mx-1"/>

        <Button 
          variant={selectedCellStyle?.bold ? "secondary" : "ghost"} 
          size="icon" 
          onClick={handleToggleBold} 
          aria-label="Bold" 
          disabled={!selectedCell}
        >
          <Bold />
        </Button>
        <Button 
          variant={selectedCellStyle?.italic ? "secondary" : "ghost"} 
          size="icon" 
          onClick={handleToggleItalic} 
          aria-label="Italic" 
          disabled={!selectedCell}
        >
          <Italic />
        </Button>
        <Button 
          variant={selectedCellStyle?.underline ? "secondary" : "ghost"} 
          size="icon" 
          onClick={handleToggleUnderline} 
          aria-label="Underline" 
          disabled={!selectedCell}
        >
          <Underline />
        </Button>
        
        {/* TODO: Add color pickers and more formatting buttons here */}
      </div>

      <Menubar className="border-none shadow-none p-0 h-auto">
        <MenubarMenu>
          <MenubarTrigger>File</MenubarTrigger>
          <MenubarContent>
            <MenubarItem onClick={saveSpreadsheet} disabled={!spreadsheet}><Save className="mr-2 h-4 w-4" /> Save</MenubarItem>
            <MenubarItem onClick={handleLoadFromFile}><Upload className="mr-2 h-4 w-4" /> Load from File...</MenubarItem>
            <MenubarSeparator />
            <MenubarItem onClick={() => handleExport('xlsx')}><Download className="mr-2 h-4 w-4" /> Export as .xlsx</MenubarItem>
            <MenubarItem onClick={() => handleExport('csv')}><Download className="mr-2 h-4 w-4" /> Export as .csv</MenubarItem>
            <MenubarSeparator />
            <MenubarItem asChild><Link href="/"><Home className="mr-2 h-4 w-4" /> Dashboard</Link></MenubarItem>
          </MenubarContent>
        </MenubarMenu>

        <MenubarMenu>
          <MenubarTrigger>Edit</MenubarTrigger>
          <MenubarContent>
            <MenubarItem onClick={undo} disabled={!canUndo}><Undo className="mr-2 h-4 w-4" /> Undo <MenubarShortcut>Ctrl+Z</MenubarShortcut></MenubarItem>
            <MenubarItem onClick={redo} disabled={!canRedo}><Redo className="mr-2 h-4 w-4" /> Redo <MenubarShortcut>Ctrl+Y</MenubarShortcut></MenubarItem>
            <MenubarSeparator />
            <MenubarItem onClick={insertRow}><RowsIcon className="mr-2 h-4 w-4" /> Insert Row</MenubarItem>
            <MenubarItem onClick={insertColumn}><Columns className="mr-2 h-4 w-4" /> Insert Column</MenubarItem>
            <MenubarSeparator />
            <MenubarItem onClick={mergeCells}><Combine className="mr-2 h-4 w-4" /> Merge Cells</MenubarItem>
          </MenubarContent>
        </MenubarMenu>

        <MenubarMenu>
          <MenubarTrigger>Format</MenubarTrigger>
          <MenubarContent>
            <MenubarItem onClick={handleToggleBold} disabled={!selectedCell} className={cn(selectedCellStyle?.bold && "bg-accent")}>
              <Bold className="mr-2 h-4 w-4" /> Bold
            </MenubarItem>
            <MenubarItem onClick={handleToggleItalic} disabled={!selectedCell} className={cn(selectedCellStyle?.italic && "bg-accent")}>
              <Italic className="mr-2 h-4 w-4" /> Italic
            </MenubarItem>
            <MenubarItem onClick={handleToggleUnderline} disabled={!selectedCell} className={cn(selectedCellStyle?.underline && "bg-accent")}>
              <Underline className="mr-2 h-4 w-4" /> Underline
            </MenubarItem>
            <MenubarSeparator />
            <MenubarItem onClick={setFontColor} disabled={!selectedCell}><Palette className="mr-2 h-4 w-4" /> Font Color...</MenubarItem>
            <MenubarItem onClick={setBackgroundColor} disabled={!selectedCell}><Palette className="mr-2 h-4 w-4" /> Background Color...</MenubarItem>
            <MenubarSeparator />
            <MenubarMenu>
                <MenubarTrigger className="w-full justify-start font-normal px-2 py-1.5 text-sm relative" disabled={!selectedCell}>
                    <AlignLeft className="mr-2 h-4 w-4" /> Align
                </MenubarTrigger>
                <MenubarContent>
                    <MenubarItem onClick={() => setTextAlign('left')} className={cn(selectedCellStyle?.textAlign === 'left' && "bg-accent")}><AlignLeft className="mr-2 h-4 w-4" /> Left</MenubarItem>
                    <MenubarItem onClick={() => setTextAlign('center')} className={cn(selectedCellStyle?.textAlign === 'center' && "bg-accent")}><AlignCenter className="mr-2 h-4 w-4" /> Center</MenubarItem>
                    <MenubarItem onClick={() => setTextAlign('right')} className={cn(selectedCellStyle?.textAlign === 'right' && "bg-accent")}><AlignRight className="mr-2 h-4 w-4" /> Right</MenubarItem>
                </MenubarContent>
            </MenubarMenu>
          </MenubarContent>
        </MenubarMenu>
      </Menubar>
      
    </div>
  );
}

    