"use client";

import { useSpreadsheet } from '@/hooks/useSpreadsheet';
import Toolbar from './Toolbar';
import SheetTabs from './SheetTabs';
import Grid from './Grid';
import FormulaBar from './FormulaBar';
import { ResizableHandle, ResizablePanel, ResizablePanelGroup } from "@/components/ui/resizable";

export default function SpreadsheetEditorLayout() {
  const { spreadsheet } = useSpreadsheet();

  if (!spreadsheet) {
    // This case should ideally be handled by the page, but as a fallback:
    return <div className="flex items-center justify-center h-screen">Spreadsheet data not available.</div>;
  }
  
  const activeSheet = spreadsheet.sheets.find(s => s.id === spreadsheet.activeSheetId);

  if (!activeSheet) {
    return <div className="flex items-center justify-center h-screen">Active sheet not found.</div>;
  }

  return (
    <div className="flex flex-col h-screen bg-background_alt overflow-hidden">
      <Toolbar spreadsheetName={spreadsheet.name} />
      <FormulaBar />
      <div className="flex-grow overflow-hidden">
        {/* Using ResizablePanelGroup for potential future resizable elements, e.g., a sidebar for formatting */}
        <ResizablePanelGroup direction="vertical">
          <ResizablePanel defaultSize={100} className="min-h-[200px]">
            <Grid sheet={activeSheet} />
          </ResizablePanel>
          {/* 
          Example for a resizable sidebar panel (if needed later)
          <ResizableHandle withHandle />
          <ResizablePanel defaultSize={25} className="min-h-[100px]">
            <div className="p-4">Sidebar for formatting options</div>
          </ResizablePanel> 
          */}
        </ResizablePanelGroup>
      </div>
      <SheetTabs sheets={spreadsheet.sheets} activeSheetId={spreadsheet.activeSheetId} />
    </div>
  );
}
