
"use client";

import type { SheetData, CellData } from '@/types/spreadsheet';
import { useSpreadsheet } from '@/hooks/useSpreadsheet';
import Cell from './Cell';
import { ScrollArea } from "@/components/ui/scroll-area";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";

interface GridProps {
  sheet: SheetData;
}

const getColumnName = (index: number): string => {
  let name = '';
  let n = index;
  do {
    name = String.fromCharCode(65 + (n % 26)) + name;
    n = Math.floor(n / 26) -1;
  } while (n >= 0)
  return name;
};


export default function Grid({ sheet }: GridProps) {
  const { updateCell, selectedCell, setSelectedCell } = useSpreadsheet();

  const handleCellChange = (rowIndex: number, colIndex: number, rawValue: string | number) => {
    updateCell(sheet.id, rowIndex, colIndex, { rawValue });
  };

  return (
    <ScrollArea className="h-full w-full p-1 bg-muted/20 focus-within:outline-none focus-within:ring-2 focus-within:ring-ring focus-within:ring-offset-2 rounded-md" tabIndex={-1}>
        <Table className="min-w-full border-collapse table-fixed">
          <TableHeader className="sticky top-0 bg-card z-10 shadow-sm">
            <TableRow>
              <TableHead className="w-12 min-w-[3rem] p-0 text-center border sticky left-0 bg-card z-20 select-none">#</TableHead>
              {Array.from({ length: sheet.columnCount }).map((_, colIndex) => (
                <TableHead key={colIndex} className="min-w-[100px] p-0 text-center border select-none">
                  {getColumnName(colIndex)}
                </TableHead>
              ))}
            </TableRow>
          </TableHeader>
          <TableBody>
            {sheet.cells.map((row, rowIndex) => (
              <TableRow key={rowIndex}>
                <TableHead className="w-12 min-w-[3rem] p-0 text-center border sticky left-0 bg-card z-10 select-none">
                  {rowIndex + 1}
                </TableHead>
                {row.map((cell, colIndex) => (
                  <Cell
                    key={cell.id}
                    cellData={cell}
                    rowIndex={rowIndex}
                    colIndex={colIndex}
                    onCellChange={handleCellChange}
                    sheetId={sheet.id}
                    isSelected={
                        !!selectedCell &&
                        selectedCell.sheetId === sheet.id &&
                        selectedCell.rowIndex === rowIndex &&
                        selectedCell.colIndex === colIndex
                    }
                    onSelect={() => setSelectedCell({ sheetId: sheet.id, rowIndex, colIndex })}
                  />
                ))}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      <div className="h-4"></div> {/* Padding at the bottom for scroll */}
    </ScrollArea>
  );
}

    